import React from 'react'
import { useEffect, useState } from 'react'
import { Link } from 'react-router-dom'
import axios from 'axios'

function Posts() {

    const [posts, setPosts] = useState([])
    const [likes, setLikes] = useState()
    const [dislikes, setDislikes] = useState()
    let likesToggle = false
    let dislikesToggle = false

    useEffect(() => {
        axios.get('/posts-api/posts/').then(res => {
            setPosts(res.data)
            // console.log(posts)
        })
    }, [])

     const setLikesHandler = async (id) => {
        likesToggle = !likesToggle
        let post = await axios.get(`/posts-api/posts/${id}`)
        setLikes(post.likes)

        likesToggle ? 
        await axios.patch(`/posts-api/posts/${id}/`, {likes: likes + 1}): 
        await axios.patch(`/posts-api/posts/${id}/`, {likes: likes - 1})
    }

    const setDislikesHandler = async (id) => {
        dislikesToggle = !dislikesToggle
        let post = await axios.get(`/posts-api/posts/${id}`)
        setDislikes(post.dislikes)

        dislikesToggle ? 
        await axios.patch(`/posts-api/posts/${id}`, {dislikes: dislikes + 1}):
        await axios.patch(`/posts-api/posts/${id}`, {dislikes: dislikes - 1})

        
    }


    return (
        <div className="container">
            <h1 className="text-left mb-4 mt-4">Social Posts</h1>

            {posts.map(post => (
                <div className="container" key={post.id}>
                    <div className="card mb-3" style={{width: '100%'}}>
                        <div className="card-body">
                            <p className="lead card-title text-left">{post.title}</p>
                            <p className="card-text text-left">{post.body.substr(0, 30)}...</p>
                            {/* <p className="card-text">By: {post.by}</p> */}
                            <p className="card-text text-left">Created at: {post.created_at}</p>
                            <button className="btn" onClick={() => setLikesHandler(post.id)}> <i className="far fa-thumbs-up"></i> </button> <span>{post.likes}</span>
                            <button className="btn" onClick={() => setDislikesHandler(post.id)}> <i className="far fa-thumbs-down"></i> </button> <span>{post.dislikes}</span>
                        </div>
                    </div>
                </div>
            ))}
        </div>
    )
}

export default Posts
